<template>

  <div class="">

      <v-container my-5>
        <v-layout class="text-xs-center" justify-space-around>
          <v-flex md8>
              <v-card class="teal lighten-5">
                <v-card-title>
                  <h2 class="teal--text text-uppercase">Front and Back Views</h2>
                </v-card-title>

                <v-card-text>
                  <v-layout row wrap  class="text-xs-center" justify-space-around>
                  <v-flex>
                  <v-progress-linear v-show="progressBarStatus" color="teal" class="white white--text" :indeterminate="true">
                  </v-progress-linear>
                  
                  </v-flex>
                </v-layout >
                  <v-layout v-show="!progressBarStatus" row wrap  class="text-xs-center" justify-space-around>
                  
                  <v-flex md5 contain>
                      <v-img :src="frontViewImage"></v-img>
                  </v-flex>

                  <v-flex md5 contain>
                      <v-img :src="backViewImage" ></v-img>
                  </v-flex>
                  
                </v-layout>

                <v-layout mt-4>
                  <v-flex>
                    <v-btn :disabled="downloadBtn" large class="teal darken-2 white--text" @click="downloadImages"> 
                      <span class="text-uppercase title"> Download</span>
                    </v-btn>  
                  </v-flex>
                  
                </v-layout>
                
                  
                </v-card-text>
              
                </v-card>
            </v-flex>

            <!-- <v-flex>
              <v-card class="teal lighten-5">
                <v-card-title>
                  <h2 class="teal--text text-uppercase">Processed - Front and Back Views</h2>
                </v-card-title>

                <v-card-text>
                  <v-img src="/backView.png" width=400px></v-img>
                </v-card-text>
              
                </v-card>
            </v-flex> -->
        </v-layout>
      </v-container>

  </div>
</template>

<script>


import { eventBus } from '../main.js' 
import axios from 'axios'

  export default {
    data() {
      return {
        frontViewImage: "",
        backViewImage: "",
        fileSuffix: "",
        downloadBtn: true,
        progressBarStatus: false
      }
    },
    methods:{
      downloadImages(){
        console.log('Downloading');
        
        axios({
          url: '/process/frontViewDownload',
          method: 'GET',
          responseType: 'blob', // important
        }).then((response) => {
          const url = window.URL.createObjectURL(new Blob([response.data]));
          const link = document.createElement('a');
          link.href = url;
          link.setAttribute('download', `frontView_${this.fileSuffix}.png`);
          document.body.appendChild(link);
          link.click();
          return axios({
                    url: '/process/backViewDownload',
                    method: 'GET',
                    responseType: 'blob', // important
                  })
        })
        .then((response) => {
          const url = window.URL.createObjectURL(new Blob([response.data]));
          const link = document.createElement('a');
          link.href = url;
          link.setAttribute('download', `backView_${this.fileSuffix}.png`);
          document.body.appendChild(link);
          link.click();
          return axios({
                    url: '/process/backViewDownload',
                    method: 'GET',
                    responseType: 'blob', // important
                  })
        })
        .catch((err) =>{

        })

      }
    },
    created(){
      // eventBus.$on('loadedFront', (data) => {
      //   this.frontViewImage = data
      //   console.log(data);
        
      // })
      // eventBus.$on('loadedBack', (data) => {
      //   this.backViewImage = data
      //   console.log(data);
      //   // this.$forceUpdate()
      // })
      eventBus.$on('removeImages', (data) => {
        // this.$forceUpdate()
        this.fileSuffix = data
        this.frontViewImage = ""
        this.backViewImage = ""
        this.progressBarStatus = true
        // this.progressBarStatus = true
      })
      eventBus.$on('refreshImages', (data) => {
        // this.$forceUpdate()
        // this.fileSuffix = data
        this.frontViewImage = "/frontView.png"
        this.backViewImage = "/backView.png"
        this.downloadBtn = false
        this.progressBarStatus = false
      })
    }
  }
  
</script>
