<template>

  <div class="home">

      <v-container my-5>
        <v-layout row wrap  class="text-xs-center" justify-space-around>
          <v-flex>
              <v-card class="teal lighten-5">
                <v-card-title>
                  <h2 class="teal--text text-uppercase">Decrypted PDF</h2>
                </v-card-title>

                <v-card-text>
                  <v-layout row wrap  class="text-xs-center" justify-space-around>
                  <v-flex>
                  <v-progress-linear v-show="progressBarStatus" color="teal" class="white white--text" :indeterminate="true">
                  </v-progress-linear>
                  
                  </v-flex>
                </v-layout >

                  <v-flex>
                    <v-btn :disabled="downloadBtn" large class="teal darken-2 white--text" @click="downloadDecryptedPDF"> 
                      <span class="text-uppercase title"> Download Decrypted PDF</span>
                    </v-btn>  
                  </v-flex>
                  

                </v-card-text>
              
                </v-card>
          </v-flex>
        </v-layout>
      </v-container>

  </div>
</template>

<script>
import axios from 'axios'
import { eventBus } from '../main.js' 

  export default {
    data() {
      return {
        fileSuffix: "",
        downloadBtn: true,
        progressBarStatus: false
      }
    },
    methods: {
      downloadDecryptedPDF(){
      axios({
          url: '/process/decryptedPDF',
          method: 'GET',
          responseType: 'blob', // important
        }).then((response) => {
          const url = window.URL.createObjectURL(new Blob([response.data]));
          const link = document.createElement('a');
          link.href = url;
          link.setAttribute('download', `${this.fileSuffix}_decrypted.pdf`);
          document.body.appendChild(link);
          link.click();
        })
    }
    },

    created(){
      // eventBus.$on('removeImages', (data) => {
      //   // this.$forceUpdate()
      //   this.progressBarStatus = true
      //   // this.progressBarStatus = true
      // })
      eventBus.$on('refreshImages', (data) => {
        // this.$forceUpdate()
        // this.fileSuffix = data
        this.frontViewImage = "/frontView.png"
        this.backViewImage = "/backView.png"
        this.downloadBtn = false
        this.progressBarStatus = false
      })
      eventBus.$on('removeImages', (data) => {
        // this.$forceUpdate()
        this.fileSuffix = data
        this.downloadBtn = false
        this.progressBarStatus = true
        // this.progressBarStatus = true
      })
    }
    
  }
</script>
