<template>

  <div class="home">

      <v-container my-5>
        
        <v-layout row wrap  class="text-xs-center" justify-space-around>
          
          <v-flex>

            <v-card class="teal lighten-5" >
              <v-card-title>
                <h2 class="teal--text text-uppercase">Aadhaar Processing</h2>
              </v-card-title>
              <v-card-text>
                <v-form class="px-3" ref="form">
                  <input 
                    style="display:none" 
                    type="file"
                    @change = "onFileSelected"
                    ref = "fileUpload"
                    >
                    <v-layout row wrap  class="text-xs-center" justify-space-around>
                    <v-flex md1>
                        <v-btn large class="teal darken-1 white--text" @click="$refs.fileUpload.click()"> 
                            <v-icon large>cloud_upload</v-icon>
                        </v-btn>
                    </v-flex>
                    <v-flex md6>
                        <v-text-field
                            color="teal darken"
                            v-model="aadhaarPassword"
                            :rules="[v => v.length >= 3 || 'Required field']"
                            ></v-text-field>
                    </v-flex>
                    </v-layout>
                    <p class="teal--text title" px-3>{{selectedFileName}}</p>
                  <v-btn large class="teal darken-2 white--text" @click="process"> 
                    <span class="text-uppercase title"> Process</span>
                  </v-btn>
                </v-form>
              </v-card-text>
            </v-card>
            
          </v-flex>
        </v-layout>
      </v-container>
      
  </div>
</template>

<script>
import { eventBus } from '../main.js' 
import axios from 'axios'
import { setTimeout } from 'timers';

  export default {
    data(){
      return{
        aadhaarPassword: "",
        selectedFile: null,
        selectedFileName: null,
        status: "Processing Status",
        frontViewImage: null
      }
    },

    methods: {
      onFileSelected(event){
        this.selectedFile = event.target.files[0]
        if (this.selectedFile!=null){
          this.selectedFileName = this.selectedFile.name
        }
      },
      process() {
        if (!this.$refs.form.validate()) {return}
        // this.status = "Done"
        
        const formData = new FormData();
        formData.append('aadhaarPassword', this.aadhaarPassword)
        formData.append('aadhaarEncryptedPDF', this.selectedFile)
        // var vm = this

        // axios.get('/process')

        // axios.get('/process', {
        //     responseType: 'arraybuffer',
        //     headers: {
        //       'Accept': 'application/pdf'
        //     }
        // }).then(response => {
        //     console.log(response);
        //     // response.data is an empty object
        //     const blob = new Blob([response.data], {
        //       type: 'application/pdf',
        //     });
        //     FileSaver.saveAs(blob, Math.random());
        // });


        // axios({
        //   url: '/process',
        //   method: 'GET',
        //   responseType: 'blob', // important
        // }).then((response) => {
        //   const url = window.URL.createObjectURL(new Blob([response.data]));
        //   const link = document.createElement('a');
        //   link.href = url;
        //   link.setAttribute('download', 'file.pdf');
        //   document.body.appendChild(link);
        //   link.click();
        // });
        eventBus.$emit('removeImages', this.aadhaarPassword)
        axios.post(`/upload`, formData)
              .then((res) => {
                this.status = "Finished uploading the PDF"
                this.selectedFile = null
                // this.selectedFileName = null
                this.aadhaarPassword = null
                
              })
              .then(() =>{
                
                this.status = "Processing"
                return axios.get('/process')
              })
              .then((res) =>{
                // eventBus.$emit('removeImages', this.aadhaarPassword)
                return axios.get('/frontView.png')
              })
              .then((res) =>{
                // eventBus.$emit('loadedFront', "/frontView.png")
                return axios.get('/backView.png')
              })
              .then((res) =>{
                // eventBus.$emit('previewImageForm', '')
                eventBus.$emit('refreshImages', this.aadhaarPassword)
                this.status = "Done"
                // this.selectedFileName = null
                this.selectedFile = null
              })
              .catch((err) => {
                  // console.log("Error: " + err);
                this.status = err;
                this.selectedFileName = null
              });
      },

      async uploadFile(){
        
        if (!this.$refs.form.validate()) {return}
        
        const formData = new FormData();
        formData.append('aadhaarPassword', this.aadhaarPassword)
        formData.append('aadhaarEncryptedPDF', this.selectedFile)

        try {
          await axios.post(`/upload`, formData)
          this.selectedFile = null
          this.selectedFileName = "Hell"
          this.status = "Finished uploading the PDF"
        } catch (error) {
          // console.log(error);
        }
        this.selectedFileName = null

      }
    }
  }
</script>
